export function addSearchControl(map) {
  const container = L.DomUtil.create('div', 'leaflet-bottom-center leaflet-bar leaflet-control');
  container.style.backgroundColor = 'white';
  container.style.padding = '4px';
  container.style.borderRadius = '4px';
  container.style.minWidth = '250px';
  container.style.display = 'flex';
  container.style.gap = '4px';
  container.style.boxShadow = '0 1px 4px rgba(0,0,0,0.3)';
  container.style.alignItems = 'center';

  const input = L.DomUtil.create('input', '', container);
  input.type = 'text';
  input.placeholder = 'Enter lat, lon';
  input.style.flex = '1';
  input.style.border = 'none';
  input.style.outline = 'none';
  input.style.padding = '4px';

  const goButton = L.DomUtil.create('button', '', container);
  goButton.textContent = 'Go';
  goButton.style.cursor = 'pointer';
  goButton.style.padding = '4px 8px';
  goButton.disabled = true;

  const closeButton = L.DomUtil.create('button', '', container);
  closeButton.textContent = '×';
  closeButton.style.cursor = 'pointer';
  closeButton.style.padding = '4px 8px';
  closeButton.style.display = 'none';

  const CoordControl = L.Control.extend({
    onAdd: function () {
      return container;
    },
    onRemove: function () {},
  });

  const control = new CoordControl({ position: 'bottomleft' });
  control.addTo(map);

  L.DomEvent.disableClickPropagation(container);
  L.DomEvent.disableScrollPropagation(container);

  let marker;

  function updateURL(lat, lon) {
    const url = new URL(window.location.href);
    url.searchParams.set('coord', `${lat},${lon}`);
    url.hash = window.location.hash; // preserve hash
    window.history.replaceState({}, '', url);
  }

  function removeCoordFromURL() {
    const url = new URL(window.location.href);
    url.searchParams.delete('coord');
    url.hash = window.location.hash; // preserve hash
    window.history.replaceState({}, '', url);
  }

  function goToCoords(val) {
    input.value = coordParam;
    goButton.disabled = false;

    const parts = val.split(',').map(s => s.trim());
    if (parts.length !== 2) {
      alert('Please enter coordinates as "lat, lon"');
      return;
    }

    const lat = parseFloat(parts[0]);
    const lon = parseFloat(parts[1]);

    if (isNaN(lat) || isNaN(lon)) {
      alert('Invalid latitude or longitude');
      return;
    }

    if (lat < -90 || lat > 90 || lon < -180 || lon > 180) {
      alert('Coordinates out of range');
      return;
    }

    const latlng = [lat, lon];
    map.setView(latlng, 15);

    if (marker) {
      marker.setLatLng(latlng);
    } else {
      marker = L.marker(latlng).addTo(map);
    }

    updateURL(lat, lon);
    closeButton.style.display = '';
  }

  function clearSearch() {
    input.value = '';
    goButton.disabled = true;
    closeButton.style.display = 'none';
    if (marker) {
      map.removeLayer(marker);
      marker = null;
    }
    removeCoordFromURL();
  }

  goButton.addEventListener('click', () => goToCoords(input.value));
  closeButton.addEventListener('click', clearSearch);

  input.addEventListener('input', () => {
    const isValid = /^\s*-?\d+(\.\d+)?\s*,\s*-?\d+(\.\d+)?\s*$/.test(input.value.trim());
    goButton.disabled = !isValid;
  });

  input.addEventListener('keydown', e => {
    if (e.key === 'Enter' && !goButton.disabled) {
      goToCoords(input.value);
    }
  });

  // On load
  const urlParams = new URLSearchParams(window.location.search);
  const coordParam = urlParams.get('coord');
  if (coordParam) {
    goToCoords(coordParam);
  }

  container.appendChild(closeButton);

  return { goToCoords };
}
