export const drawnLines = [];

export function addHighlightPen(map) {
  let currentLine = null;
  let drawing = false;
  let isActivelyDrawing = false;
  let downloadBtn = null;

  const polylineOptions = map => {
    const zoom = map.getZoom();
    const baseWeight = 25;
    const scaledWeight = Math.max(4, baseWeight - (18 - zoom) * 2);
    return {
      color: 'yellow',
      weight: scaledWeight,
      opacity: 0.3,
    };
  };

  const controls = L.control({ position: 'bottomleft' });

  controls.onAdd = function () {
    const container = L.DomUtil.create('div', 'leaflet-bar leaflet-control');

    const drawBtn = L.DomUtil.create('a', '', container);
    drawBtn.href = '#';
    drawBtn.innerHTML = '🖊️';
    drawBtn.title = 'Toggle highlighter';
    drawBtn.style.padding = '6px 10px';
    drawBtn.style.fontSize = '20px';

    const clearBtn = L.DomUtil.create('a', '', container);
    clearBtn.href = '#';
    clearBtn.innerHTML = '🧽';
    clearBtn.title = 'Clear highlights';
    clearBtn.style.padding = '6px 10px';
    clearBtn.style.fontSize = '20px';

    downloadBtn = L.DomUtil.create('a', '', container);
    downloadBtn.href = '#';
    downloadBtn.innerHTML = '⬇️';
    downloadBtn.title = 'Download GPX';
    downloadBtn.style.padding = '6px 10px';
    downloadBtn.style.fontSize = '20px';
    downloadBtn.classList.add('leaflet-disabled');
    downloadBtn.style.opacity = '0.3';
    downloadBtn.style.pointerEvents = 'none';

    drawBtn.onclick = e => {
      e.preventDefault();
      drawing = !drawing;
      if (!drawing) {
        currentLine = null; // Reset currentLine only when drawing mode is toggled off
        isActivelyDrawing = false;
      }
      updateDrawButton();
      if (!drawing) {
        enableMapInteractions();
      }
    };

    clearBtn.onclick = e => {
      e.preventDefault();
      drawnLines.forEach(line => map.removeLayer(line));
      drawnLines.length = 0;
      currentLine = null;
      isActivelyDrawing = false;
      updateDownloadButtonState();
    };

    downloadBtn.onclick = e => {
      e.preventDefault();
      if (!downloadBtn.classList.contains('leaflet-disabled')) {
        downloadAsGpx();
      }
    };

    L.DomEvent.disableClickPropagation(container);
    L.DomEvent.disableScrollPropagation(container);

    function updateDrawButton() {
      if (drawing) {
        drawBtn.style.background = '#ff0';
        drawBtn.style.color = 'black';
      } else {
        drawBtn.style.background = 'white';
        drawBtn.style.color = '';
      }
    }

    return container;
  };

  controls.addTo(map);

  function disableMapInteractions() {
    map.dragging.disable();
    map.doubleClickZoom.disable();
    map.boxZoom.disable();
    map.scrollWheelZoom.disable();
  }

  function enableMapInteractions() {
    map.dragging.enable();
    map.doubleClickZoom.enable();
    map.boxZoom.enable();
    map.scrollWheelZoom.enable();
  }

  function getLatLngFromEvent(e) {
    if (e.latlng) return e.latlng;
    if (e.touches && e.touches.length > 0) {
      return map.containerPointToLatLng(map.mouseEventToContainerPoint(e.touches[0]));
    }
    return null;
  }

  function startDrawing(e) {
    if (!drawing) return;
    const latlng = getLatLngFromEvent(e);
    if (!latlng) return;

    if (!isActivelyDrawing) {
      isActivelyDrawing = true;
      disableMapInteractions();
      if (!currentLine) {
        currentLine = L.polyline([latlng], polylineOptions(map)).addTo(map);
        drawnLines.push(currentLine);
      } else {
        currentLine.addLatLng(latlng);
      }
    }

    if (e.preventDefault) e.preventDefault();
  }

  function continueDrawing(e) {
    if (!drawing || !isActivelyDrawing || !currentLine) return;
    const latlng = getLatLngFromEvent(e);
    if (!latlng) return;
    currentLine.addLatLng(latlng);
    if (e.preventDefault) e.preventDefault();
  }

  function endDrawing(e) {
    if (drawing && isActivelyDrawing) {
      isActivelyDrawing = false;
      updateDownloadButtonState();
      enableMapInteractions();
    }
    if (e.preventDefault) e.preventDefault();
  }

  // Mouse events
  map.on('mousedown', startDrawing);
  map.on('mousemove', continueDrawing);
  map.on('mouseup', endDrawing);

  // Touch events
  const mapContainer = map.getContainer();
  mapContainer.addEventListener('touchstart', startDrawing, { passive: true });
  mapContainer.addEventListener('touchmove', continueDrawing, { passive: true });
  mapContainer.addEventListener('touchend', endDrawing, { passive: true });

  map.on('zoomend', () => {
    drawnLines.forEach(line => line.setStyle(polylineOptions(map)));
  });

  window.addEventListener('keydown', e => {
    if (e.key.toLowerCase() === 'd') {
      drawing = !drawing;
      if (!drawing) {
        currentLine = null; // Reset currentLine when drawing mode is toggled off
        isActivelyDrawing = false;
      }
      updateDrawButton();
      if (!drawing) {
        enableMapInteractions();
      }
    }

    if ((e.ctrlKey || e.metaKey) && e.key.toLowerCase() === 'z') {
      if (drawnLines.length > 0) {
        const lastLine = drawnLines.pop();
        map.removeLayer(lastLine);
        if (currentLine === lastLine) {
          currentLine = null;
          isActivelyDrawing = false;
        }
        saveToUrl();
        updateDownloadButtonState();
      }
    }
  });

  function updateDrawButton() {
    const drawBtn = document.querySelector('.leaflet-bar a[title="Toggle highlighter"]');
    if (!drawBtn) return;
    if (drawing) {
      drawBtn.style.background = '#ff0';
      drawBtn.style.color = 'black';
    } else {
      drawBtn.style.background = 'white';
      drawBtn.style.color = '';
    }
  }

  function updateDownloadButtonState() {
    if (!downloadBtn) return;
    if (drawnLines.length) {
      downloadBtn.classList.remove('leaflet-disabled');
      downloadBtn.style.opacity = '1';
      downloadBtn.style.pointerEvents = 'auto';
    } else {
      downloadBtn.classList.add('leaflet-disabled');
      downloadBtn.style.opacity = '0.3';
      downloadBtn.style.pointerEvents = 'none';
    }
  }

  function loadFromUrl() {
    const params = new URLSearchParams(window.location.search);
    const raw = params.get('data');
    if (!raw) return;

    const segments = raw.split(';');
    segments.forEach(encoded => {
      try {
        const coords = polyline.decode(encoded).map(([lat, lng]) => L.latLng(lat, lng));
        const line = L.polyline(coords, polylineOptions(map)).addTo(map);
        drawnLines.push(line);
      } catch (e) {
        console.warn('Invalid polyline segment:', encoded);
      }
    });

    updateDownloadButtonState();
  }

  function loadDrawingFromGpx(gpxString) {
    try {
      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(gpxString, 'text/xml');

      // Check for parsing errors
      const parserError = xmlDoc.querySelector('parsererror');
      if (parserError) {
        console.warn('Invalid GPX string:', parserError.textContent);
        return false;
      }

      // Verify creator attribute
      const gpxElement = xmlDoc.querySelector('gpx');
      if (!gpxElement || gpxElement.getAttribute('creator') !== 'MapDrawer') {
        // console.warn('GPX file not created by MapDrawer or invalid GPX structure');
        return false;
      }

      // Clear existing lines
      drawnLines.forEach(line => map.removeLayer(line));
      drawnLines.length = 0;
      currentLine = null;
      drawing = false;
      // updateDrawButton();
      // updateDownloadButtonState();

      // Process track segments
      const trackSegments = xmlDoc.querySelectorAll('trkseg');
      trackSegments.forEach(segment => {
        const coords = [];
        const trackPoints = segment.querySelectorAll('trkpt');
        trackPoints.forEach(point => {
          const lat = parseFloat(point.getAttribute('lat'));
          const lon = parseFloat(point.getAttribute('lon'));
          if (!isNaN(lat) && !isNaN(lon)) {
            coords.push(L.latLng(lat, lon));
          }
        });
        if (coords.length > 0) {
          const line = L.polyline(coords, polylineOptions(map)).addTo(map);
          drawnLines.push(line);
        }
      });

      if (drawnLines.length > 0) {
        const allCoords = drawnLines.flatMap(line => line.getLatLngs());
        map.fitBounds(L.latLngBounds(allCoords));
      }

      // Update download button state
      updateDownloadButtonState();

      return true;
    } catch (e) {
      console.warn('Error parsing GPX string:', e);
    }

    return false;
  }

  function downloadAsGpx() {
    if (!drawnLines.length) return;

    // Get current UTC timestamp
    const now = new Date();
    const timestamp = now.toISOString(); // e.g., 2025-07-17T11:04:00.000Z

    const gpxHeader = `<?xml version="1.0" encoding="UTF-8"?>
<gpx version="1.1" creator="MapDrawer" xmlns="http://www.topografix.com/GPX/1/1">
  <trk>
    <name>Manually Drawn Trace</name>
    <desc>Manually drawn trace for mapping purposes, not GPS-accurate.</desc>`;

    const gpxFooter = `</trk></gpx>`;

    const segments = drawnLines
      .map(line => {
        const pts = line
          .getLatLngs()
          .map(
            p =>
              `<trkpt lat="${p.lat.toFixed(6)}" lon="${p.lng.toFixed(
                6
              )}"><time>${timestamp}</time></trkpt>`
          )
          .join('\n');
        return `<trkseg>\n${pts}\n</trkseg>`;
      })
      .join('\n');

    const gpx = `${gpxHeader}\n${segments}\n${gpxFooter}`;
    const blob = new Blob([gpx], { type: 'application/gpx+xml' });
    const url = URL.createObjectURL(blob);

    const a = document.createElement('a');
    a.href = url;
    a.download = 'drawing.gpx';
    a.click();

    URL.revokeObjectURL(url);
  }

  return { loadDrawingFromGpx };
}
