import { regionBounds } from './config.js';
import { overlays } from './layers.js';
import { speeds as speedsColors, colors } from './config.js';

export function enableGpxDragAndDrop(map, layersControl, loadDrawingFromGpx) {
  const mapContainer = map.getContainer();
  const bounds = L.latLngBounds(regionBounds);

  ['dragenter', 'dragover', 'dragleave', 'drop'].forEach(eventName => {
    mapContainer.addEventListener(eventName, e => e.preventDefault(), false);
  });

  mapContainer.addEventListener('drop', e => {
    e.preventDefault();
    if (!e.dataTransfer.files.length) return;

    for (const file of e.dataTransfer.files) {
      if (!file.name.toLowerCase().endsWith('.gpx')) continue;

      const reader = new FileReader();
      reader.onload = event => {
        const gpxText = event.target.result;
        if (loadDrawingFromGpx(gpxText)) {
          return;
        }

        // Remove previous uploaded GPX layers
        for (const overlayName in overlays) {
          if (overlays.hasOwnProperty(overlayName) && overlays[overlayName] instanceof L.GeoJSON) {
            layersControl.removeLayer(overlays[overlayName]);
            map.removeLayer(overlays[overlayName]);
            delete overlays[overlayName];
          }
        }

        const geojson = loadFromGpx(gpxText);
        if (geojson) {
          // Create GeoJSON layer with custom styles
          const layer = L.geoJSON(geojson, {
            style: feature => ({
              color: feature.properties.stroke,
              opacity: feature.properties['stroke-opacity'],
              weight: feature.properties['stroke-width'],
            }),
            onEachFeature: function (f, layer) {
              layer.bindTooltip(
                `<b>Speed:</b> ${f.properties.speed} km/h<br><b>Time:</b> ${f.properties.time}`
              );
            },
          }).addTo(map);

          // Add slider control for speed divider
          addSpeedDividerSlider(map, layer);

          const gpxBounds = layer.getBounds();
          if (bounds.intersects(gpxBounds)) {
            map.fitBounds(gpxBounds);
            // Add to overlays and control
            const name = file.name.replace(/\.gpx$/i, '');
            overlays[name] = layer;
            layersControl.addOverlay(layer, name);
          } else {
            alert(
              `GPX file "${file.name}" is outside the allowed bounds and will not be displayed.`
            );
          }
          return;
        }
      };
      reader.readAsText(file);
    }
  });

  function loadFromGpx(gpxString) {
    const parser = new DOMParser();
    const xmlDoc = parser.parseFromString(gpxString, 'text/xml');

    const parserError = xmlDoc.querySelector('parsererror');
    if (parserError) {
      console.warn('Invalid GPX string:', parserError.textContent);
      return null;
    }

    function haversineDistance(lat1, lon1, lat2, lon2) {
      const R = 6371;
      const dLat = ((lat2 - lat1) * Math.PI) / 180;
      const dLon = ((lon2 - lon1) * Math.PI) / 180;
      const a =
        Math.sin(dLat / 2) ** 2 +
        Math.cos((lat1 * Math.PI) / 180) *
          Math.cos((lat2 * Math.PI) / 180) *
          Math.sin(dLon / 2) ** 2;
      const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
      return R * c;
    }

    const geojson = {
      type: 'FeatureCollection',
      features: [],
    };

    let trackSegments = xmlDoc.querySelectorAll('trkseg');
    if (!trackSegments.length) trackSegments = xmlDoc.querySelectorAll('rte');
    trackSegments.forEach(segment => {
      let trackPoints = segment.querySelectorAll('trkpt');
      if (!trackPoints.length) trackPoints = xmlDoc.querySelectorAll('rtept');

      for (let i = 1; i < trackPoints.length; i++) {
        const prevPoint = trackPoints[i - 1];
        const currPoint = trackPoints[i];

        const lat1 = parseFloat(prevPoint.getAttribute('lat'));
        const lon1 = parseFloat(prevPoint.getAttribute('lon'));
        const lat2 = parseFloat(currPoint.getAttribute('lat'));
        const lon2 = parseFloat(currPoint.getAttribute('lon'));
        if ([lat1, lon1, lat2, lon2].some(isNaN)) continue;

        let speedKmh = null;
        const time1 = prevPoint.querySelector('time');
        const time2 = currPoint.querySelector('time');
        if (time1 && time2) {
          try {
            const t1 = new Date(time1.textContent);
            const t2 = new Date(time2.textContent);
            const timeDiffHours = (t2 - t1) / (1000 * 3600);
            if (timeDiffHours > 0) {
              const distanceKm = haversineDistance(lat1, lon1, lat2, lon2);
              speedKmh = distanceKm / timeDiffHours;
            }
          } catch (e) {
            speedKmh = null;
          }
        }

        const colorConfig =
          speedKmh === null
            ? { color: colors.likely }
            : speedsColors.find(config => speedKmh <= config.max) ||
              speedsColors[speedsColors.length - 1];
        const color = colorConfig.color;

        const coords = [
          [lon1, lat1],
          [lon2, lat2],
        ];

        geojson.features.push({
          type: 'Feature',
          geometry: {
            type: 'LineString',
            coordinates: coords,
          },
          properties: {
            baseSpeed: speedKmh ?? null,
            speed: speedKmh ? speedKmh.toFixed(2) : null,
            time: time1 ? time1.textContent : null,
            stroke: color,
            'stroke-opacity': 0.8,
            'stroke-width': 7,
          },
        });
      }
    });

    return geojson.features.length > 0 ? geojson : null;
  }

  function addSpeedDividerSlider(map, layer) {
    // Prevent adding multiple sliders
    if (document.getElementById('speed-multiplier')) return;

    const slider = L.control({ position: 'topright' });

    slider.onAdd = function (map) {
      const div = L.DomUtil.create('div', 'leaflet-bar');
      div.style.background = 'white';
      div.style.padding = '6px 10px';
      div.style.fontSize = '14px';
      div.style.lineHeight = '1.2';
      div.style.minWidth = '160px';

      div.innerHTML = `
        <label>
          Speed ÷ <span id="multiplier-val">1.00</span><br/>
          <input id="speed-multiplier" type="range" min="0.1" max="2" step="0.01" value="1" style="width: 140px;" />
        </label>
      `;

      L.DomEvent.disableClickPropagation(div);
      return div;
    };

    slider.addTo(map);

    const sliderInput = document.getElementById('speed-multiplier');
    const multiplierVal = document.getElementById('multiplier-val');

    sliderInput.addEventListener('input', e => {
      const divider = parseFloat(e.target.value);
      multiplierVal.textContent = divider.toFixed(2);

      layer.eachLayer(segment => {
        const baseSpeed = segment.feature.properties.baseSpeed;
        if (baseSpeed == null) return;

        const newSpeed = baseSpeed / divider;
        segment.feature.properties.speed = newSpeed.toFixed(2);

        const colorConfig =
          speedsColors.find(cfg => newSpeed <= cfg.max) || speedsColors[speedsColors.length - 1];
        const color = colorConfig.color;

        segment.setStyle({ color });

        segment.setTooltipContent(
          `<b>Speed:</b> ${newSpeed.toFixed(2)} km/h<br><b>Time:</b> ${
            segment.feature.properties.time
          }`
        );
      });
    });
  }
}
